		//支付倒计时
		countDown: function() {
			uni.hideLoading()
			var interval = setInterval(() => {
				--this.countDownDate;
				uni.setStorage({
					key: 'countDown',
					data: this.countDownDate,
				})
				if (this.countDownDate <= 0) {
					clearInterval(interval)
					this.countDownEnd();
					uni.removeStorage({
						key: 'countDown'
					})
				}
			}, 1000)
		},

		//倒计时结束
		countDownEnd: function() {
			uni.request({
				url: 'http://218.67.107.93:9210/api/app/getScenicspotOrderDetail?orderNumber=' + this.orderInfo.orderNumber,
				method: 'POST',
				success: (res) => {
					// console.log(res)
					if (res.data.data.orderType == '待支付') {
						uni.request({
							url: 'http://218.67.107.93:9210/api/app/returnOrder?orderNumber=' + res.data.data.orderNumber,
							method: 'POST',
							success() {
								// console.log('取消成功')
								uni.showToast({
									title: '支付超时，已自动取消订单',
									icon: 'none',
									duration: 2000
								})
								uni.getStorage({
									key: 'returnIndex',
									success: (res) => {
										if (res.data == false) {
											uni.switchTab({
												url: '../../order/OrderList'
											})
										}
									}
								})

							},
							fail() {
								// console.log('取消失败')
								uni.getStorage({
									key: 'returnIndex',
									success: (res) => {
										if (res.data == false) {
											uni.switchTab({
												url: '../../order/OrderList'
											})
										}
									}
								})
							}
						})
					} else {
						return false
					}
				}
			})

		},
		
		//禁止重复提交操作
		paymentSatas:function(){
